'use client';
import { useEffect, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { moviesApi, showsApi, bookingsApi } from '@/lib/api';
import { useAuth } from '@/context/auth-context';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { format } from 'date-fns';

interface Movie {
    id: string;
    title: string;
    poster_url: string;
    duration: number;
}

interface Theater {
    id: string;
    name: string;
    city: string;
    location: string;
}

interface Show {
    id: string;
    start_time: string;
    price: number;
    movies: Movie;
    theaters: Theater;
}

export default function BookingPage() {
    const searchParams = useSearchParams();
    const movieId = searchParams.get('movie_id');
    const router = useRouter();
    const { isAuthenticated } = useAuth();

    const [movie, setMovie] = useState<Movie | null>(null);
    const [shows, setShows] = useState<Show[]>([]);
    const [selectedShow, setSelectedShow] = useState<Show | null>(null);
    const [selectedSeats, setSelectedSeats] = useState<string[]>([]);
    const [loading, setLoading] = useState(true);
    const [booking, setBooking] = useState(false);
    const [error, setError] = useState('');
    const [success, setSuccess] = useState(false);

    useEffect(() => {
        if (movieId) {
            fetchMovieAndShows();
        }
    }, [movieId]);

    const fetchMovieAndShows = async () => {
        try {
            const [movieData, showsData] = await Promise.all([
                moviesApi.getById(movieId!),
                showsApi.getAll({ movie_id: movieId! })
            ]);
            setMovie(movieData);
            setShows(showsData);
        } catch (err: any) {
            setError(err.message || 'Failed to load booking data');
        } finally {
            setLoading(false);
        }
    };

    const generateSeats = () => {
        const rows = ['A', 'B', 'C', 'D', 'E', 'F'];
        const seatsPerRow = 10;
        const seats = [];
        for (const row of rows) {
            for (let i = 1; i <= seatsPerRow; i++) {
                seats.push(`${row}${i}`);
            }
        }
        return seats;
    };

    const toggleSeat = (seat: string) => {
        setSelectedSeats(prev =>
            prev.includes(seat)
                ? prev.filter(s => s !== seat)
                : [...prev, seat]
        );
    };

    const handleBooking = async () => {
        if (!isAuthenticated) {
            router.push('/login');
            return;
        }

        if (!selectedShow || selectedSeats.length === 0) {
            setError('Please select a show and at least one seat');
            return;
        }

        setBooking(true);
        setError('');

        try {
            await bookingsApi.create({
                show_id: selectedShow.id,
                seat_numbers: selectedSeats,
                total_price: selectedShow.price * selectedSeats.length
            });
            setSuccess(true);
            setTimeout(() => router.push('/'), 2000);
        } catch (err: any) {
            setError(err.message || 'Booking failed');
        } finally {
            setBooking(false);
        }
    };

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="text-xl">Loading...</div>
            </div>
        );
    }

    if (success) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-green-50">
                <Card className="max-w-md">
                    <CardContent className="p-8 text-center">
                        <div className="text-6xl mb-4">ðŸŽ‰</div>
                        <h2 className="text-2xl font-bold text-green-600 mb-2">Booking Confirmed!</h2>
                        <p className="text-gray-600">Redirecting to home...</p>
                    </CardContent>
                </Card>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50 p-6">
            <div className="max-w-6xl mx-auto">
                <h1 className="text-3xl font-bold mb-6">Book Tickets</h1>

                {movie && (
                    <Card className="mb-6">
                        <CardHeader>
                            <CardTitle>{movie.title}</CardTitle>
                        </CardHeader>
                    </Card>
                )}

                {/* Show Selection */}
                <Card className="mb-6">
                    <CardHeader>
                        <CardTitle>Select Show</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                            {shows.map((show) => (
                                <Button
                                    key={show.id}
                                    variant={selectedShow?.id === show.id ? 'default' : 'outline'}
                                    className="h-auto p-4 flex flex-col items-start"
                                    onClick={() => setSelectedShow(show)}
                                >
                                    <div className="font-bold">{show.theaters.name}</div>
                                    <div className="text-sm">{show.theaters.city}</div>
                                    <div className="text-sm mt-2">
                                        {format(new Date(show.start_time), 'MMM dd, h:mm a')}
                                    </div>
                                    <div className="text-sm font-semibold mt-1">â‚¹{show.price}</div>
                                </Button>
                            ))}
                        </div>
                    </CardContent>
                </Card>

                {/* Seat Selection */}
                {selectedShow && (
                    <Card className="mb-6">
                        <CardHeader>
                            <CardTitle>Select Seats</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="mb-4 text-center">
                                <div className="inline-block bg-gray-300 px-12 py-2 rounded-t-full">
                                    SCREEN
                                </div>
                            </div>
                            <div className="grid grid-cols-10 gap-2 max-w-2xl mx-auto">
                                {generateSeats().map((seat) => (
                                    <Button
                                        key={seat}
                                        size="sm"
                                        variant={selectedSeats.includes(seat) ? 'default' : 'outline'}
                                        onClick={() => toggleSeat(seat)}
                                        className="h-8"
                                    >
                                        {seat}
                                    </Button>
                                ))}
                            </div>
                            <div className="mt-6 flex justify-between items-center">
                                <div>
                                    <span className="font-semibold">Selected: </span>
                                    {selectedSeats.join(', ') || 'None'}
                                </div>
                                <div className="text-xl font-bold">
                                    Total: â‚¹{selectedShow.price * selectedSeats.length}
                                </div>
                            </div>
                        </CardContent>
                    </Card>
                )}

                {error && (
                    <div className="mb-4 p-4 bg-red-50 text-red-600 rounded-md">
                        {error}
                    </div>
                )}

                <Button
                    onClick={handleBooking}
                    disabled={!selectedShow || selectedSeats.length === 0 || booking}
                    size="lg"
                    className="w-full"
                >
                    {booking ? 'Processing...' : 'Confirm Booking'}
                </Button>
            </div>
        </div>
    );
}
